.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
}

.Container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  text-align: center;
}

/* üñºÔ∏è Imagen tama√±o medio */
img {
  max-width: 250px;
  height: auto;
  object-fit: cover;
}

/* üîò Bot√≥n tama√±o medio */
button {
  background-color: crimson;
  color: #fff;
  margin-top: 10px;
  padding: 8px 28px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

button:hover {
  background-color: #b30f2b;
  transform: scale(1.05);
}

.gap-1 {
  gap: 0.75em;
}

.flex {
  display: flex;
}

.w-full {
  width: 100%;
}

.align-items-center {
  align-items: center;
}

.justify-content-center {
  justify-content: center;
}

.grid {
  display: grid;
}

/* üïí Temporizador tama√±o intermedio */
.timer {
  font-size: clamp(26px, 3.5vw, 34px);
  font-weight: bold;
  color: #222;
  text-align: center;
}

/* ‚è∞ Mensaje tama√±o medio */
.timeout-message {
  font-size: clamp(22px, 3.5vw, 28px);
  color: rgb(0, 0, 0);
  text-align: center;
  white-space: nowrap;
}
